#root {
  width: 100%;
  max-width: 1900px;
  padding: 0 20px 0 210px;
  @media (max-width: 800px) {
    padding: 0 15px 0 15px;
  }
}

.box {
  &__languageCV {
    @include position(absolute, 95, 10px, initial, initial, 10px);
    display: flex;
    flex-direction: column;
    align-items: flex-end;
  }
  &__btnLinkCV {
    position: relative;
  }
  &__linkCV {
    @include position(absolute, 0, 160%, initial, initial, 20%);
    display: flex;
    align-items: center;
    justify-content: space-around;
    visibility: hidden;
    opacity: 0;
    width: 140%;
    padding: 8px 12px 12px 12px;
    border-radius: 3px;
    border-radius: 100%;
    @include transitions((visibility, opacity));
    &::after,
    &::before {
      content: "";
      border-radius: 100%;
      box-shadow: inherit;
      background-color: inherit;
    }
    &::before {
      @include position(absolute, 0, -11px, initial, initial, 8px);
      @include block-size(20px, 10px);
    }
    &::after {
      @include position(absolute, 0, -18px, initial, initial, 8px);
      @include block-size(10px, 5px);
    }
    &-active {
      visibility: visible;
      opacity: 1;
    }
  }
  @media (max-width: 600px) {
    &__linkCV {
      top: 180%;
      width: 150%;
      padding: 6px 12px 12px 12px;
    }
  }
  @media (max-width: 600px) {
    &__linkCV {
      width: 160%;
      padding: 6px 10px 10px 10px;
    }
  }
}

.btn {
  &-language,
  &-CV {
    display: flex;
    align-items: center;
    overflow: hidden;
    border: 0;
    padding: 0;
    background-color: transparent;
    font-size: 1.1rem;
    opacity: 0.6;
    transform: scale(1);
    @include transitions((opacity, transform));
    & svg {
      @include block-size(35px, 35px);
    }
    &:hover,
    &:focus,
    &:active {
      opacity: 1;
      transform: scale(1.01);
    }
  }
  &-language {
    margin-bottom: 8px;
    & svg {
      margin-left: 10px;
    }
  }
  &-CV {
    font-weight: 700;
    & svg {
      margin-left: 8px;
    }
  }
  &-scroll {
    @include position(fixed, 90, 0, 0);
    @include block-size(100%, auto);
    border: 0;
    padding: 10px 0 5px 0;
    background-color: #1b27352f;
    box-shadow: 0 0.5px 20px 0 #38495a58;
    @include transition(visibility);
    @include visible();
    & svg {
      transform: rotate(-90deg);
      animation: arrow 1s ease-in-out infinite;
      @keyframes arrow {
        0% {
          transform: rotate(-90deg) translateX(0);
        }
        50% {
          transform: rotate(-90deg) translateX(5px);
        }
        100% {
          transform: rotate(-90deg) translateX(0);
        }
      }
    }
  }
  @media (max-width: 800px) {
    &-language,
    &-CV {
      & svg {
        @include block-size(30px, 30px);
      }
    }
    &-language {
      margin-bottom: 5px;
      & svg {
        margin-left: 8px;
      }
    }
    &-CV svg {
      margin-left: 6px;
    }
  }
  @media (max-width: 600px) {
    &-language,
    &-CV {
      & svg {
        @include block-size(25px, 25px);
      }
    }
    &-language {
      margin-bottom: 1px;
      & svg {
        margin-left: 5px;
      }
    }
    &-CV svg {
      margin-left: 4px;
    }
  }
}

.link {
  &-CV {
    display: inline-block;
    font-weight: 700;
    &:first {
      margin-right: 10px;
    }
  }
  @media (max-width: 450px) {
    &-CV {
      font-size: 1.4rem;
    }
  }
}

.section {
  padding: 20px 6px;
  overflow: hidden;
  @media (max-width: 800px) {
    padding: 20px 6px 20px 0;
  }
}

.title {
  @include position(relative, 5);
  max-width: 450px;
  margin: 0 0 30px 0;
  @include border(0 0 2px 0);
  padding: 0 0 20px 10px;
  &__active {
    @include visible-translate-Y(-50px);
  }
  @media (max-width: 600px) {
    max-width: 300px;
    padding: 0 0 15px 10px;
  }
  @media (max-width: 450px) {
    max-width: 218px;
    margin: 0 0 20px 0;
    padding: 0 0 8px 8px;
  }
}

.active {
  @mixin active-color($prop) {
    &-1 {
      #{$prop}: #fff;
    }
    &-2 {
      #{$prop}: #38495a;
    }
    &-3 {
      #{$prop}: #1b2735;
    }
  }
  &-stroke {
    @include active-color(stroke);
  }
  &-fill {
    @include active-color(fill);
  }
  &-stopColor {
    @include active-color(stop-color);
  }
  &-borderColor {
    @include active-color(border-color);
  }
  &-background {
    @include active-color(background-color);
    &-focus {
      &:hover,
      &:focus {
        background-color: #38495a;
      }
      &:active {
        background-color: #090a0fcd;
      }
    }
  }
  &-color {
    @include active-color(color);
    &-effect {
      background: -webkit-linear-gradient(#fff, #38495a);
      background-clip: text;
      -webkit-text-fill-color: transparent;
    }
    &-focus {
      &:hover,
      &:focus,
      &:active {
        color: #485e74;
      }
    }
  }
  &-gradient {
    background: radial-gradient(ellipse at bottom, #1b2735 0%, #090a0f 100%);
  }
  &-boxShadow {
    &-1 {
      box-shadow: 2px -2px 5px #ffffff81, 2px 2px 5px #ffffff81;
    }
    &-2 {
      box-shadow: 0 5px 15px 0 #38495a, 5px 0 15px 0 #38495a;
    }
  }
  &-textShadow {
    &-1 {
      text-shadow: 0.9px 0.9px #85acd4, 1.2px 1.2px #59738e;
      @media (max-width: 450px) {
        text-shadow: 0.5px 0.5px #85acd4, 1px 1px #59738e;
      }
    }
  }
}

// contacts-map
[class*="ground-pane"] {
  filter: grayscale(90%);
}

// Lazy-load image
.lazyloading,
.lazyloaded {
  &::after {
    opacity: 0;
    visibility: hidden;
  }
}

.lazyload {
  text-align: center;
  font-size: 0;
  &::after {
    opacity: 1;
    visibility: visible;
  }
}
