.stars,
.twinkling {
  @include position(fixed, 0, 0, 0, 0, 0);
  display: block;
}

.space-background {
  @include position(fixed, 2, 0, 0);
  @include block-size(100%, 100%);
  opacity: 0.7;
  background: radial-gradient(circle at 100%, #1b2735 0%, #090a0f 70%, #38495a 100%);
}

.stars {
  z-index: 0;
  background: #000 url("../image/background/starts.webp") repeat top center;
}

.twinkling {
  z-index: 1;
  background: transparent url("../image/background/shadow.webp") repeat top center;
  transform: translate(-100px, 50px);
}
