@import "../../assets/style/mixins.scss";

.main {
  @include position(fixed, 90, initial, initial, 10px, 10px);
  display: flex;
  align-items: center;
  margin: 0;
  padding: 9px;
  border-radius: 5px;
  font-weight: 600;
  @include visible();
  &__hidden {
    @include hidden();
  }
  & svg {
    @include block-size(38px, 25px);
    margin-right: 7px;
  }
  @media (max-width: 800px) {
    right: initial;
    left: 8px;
    bottom: 8px;
    padding: 8px;
    & svg {
      @include block-size(30px, 20px);
      margin-right: 5px;
    }
  }
  @media (max-width: 600px) {
    padding: 7px;
    font-size: 0.8rem;
    & svg {
      @include block-size(20px, 15px);
    }
  }
  @media (max-width: 450px) {
    left: 5px;
    bottom: 5px;
    padding: 4px;
    & svg {
      @include block-size(18px, 15px);
    }
  }
}
